.member-management {
  min-height: calc(100vh - 70px);
  padding: var(--spacing-2xl) 0;
  background: var(--off-white);
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: var(--spacing-xl);
}

.page-header h1 {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-xs);
}

.success-banner {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-md);
  background: rgba(76, 175, 80, 0.1);
  border: 1px solid var(--success);
  border-radius: var(--radius-md);
  color: var(--success);
  margin-bottom: var(--spacing-xl);
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.stat-card {
  background: var(--white);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  box-shadow: var(--shadow-sm);
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: var(--radius-md);
  background: var(--light-gray);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--gray);
}

.stat-icon.active {
  background: rgba(76, 175, 80, 0.1);
  color: var(--success);
}

.stat-icon.disabled {
  background: rgba(244, 67, 54, 0.1);
  color: var(--danger);
}

.stat-icon.owner {
  background: rgba(255, 107, 53, 0.1);
  color: var(--primary);
}

.stat-content {
  display: flex;
  flex-direction: column;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--secondary);
}

.stat-label {
  font-size: 0.875rem;
  color: var(--gray);
}

.filters-section {
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.search-bar {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  background: var(--light-gray);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-md);
}

.search-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
}

.filter-controls {
  display: flex;
  gap: var(--spacing-lg);
  flex-wrap: wrap;
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}

.filter-group label {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--gray);
  text-transform: uppercase;
}

.filter-select {
  padding: var(--spacing-sm) var(--spacing-md);
  border: 1px solid var(--light-gray);
  border-radius: var(--radius-md);
  background: var(--white);
  min-width: 150px;
  cursor: pointer;
}

.members-table-container {
  overflow-x: auto;
}

.members-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 900px;
}

.members-table th {
  text-align: left;
  padding: var(--spacing-md);
  background: var(--light-gray);
  font-weight: 600;
  font-size: 0.75rem;
  text-transform: uppercase;
  color: var(--gray);
}

.members-table td {
  padding: var(--spacing-md);
  border-bottom: 1px solid var(--light-gray);
  vertical-align: middle;
}

.members-table tr:hover {
  background: var(--off-white);
}

.members-table tr.disabled-row {
  opacity: 0.6;
  background: rgba(244, 67, 54, 0.03);
}

.members-table tr.current-user-row {
  background: rgba(76, 175, 80, 0.05);
}

.member-cell {
  min-width: 250px;
}

.member-info {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

.member-avatar {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-full);
  object-fit: cover;
}

.member-avatar-placeholder {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-full);
  background: var(--gradient-primary);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 0.875rem;
}

.member-details {
  display: flex;
  flex-direction: column;
}

.member-name {
  font-weight: 600;
  color: var(--secondary);
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
}

.you-badge {
  font-size: 0.625rem;
  padding: 2px 6px;
  background: var(--success);
  color: var(--white);
  border-radius: var(--radius-sm);
  font-weight: 600;
}

.member-email {
  font-size: 0.875rem;
  color: var(--gray);
}

.role-badge {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: capitalize;
}

.role-player {
  background: rgba(33, 150, 243, 0.1);
  color: #2196F3;
}

.role-owner {
  background: rgba(255, 107, 53, 0.1);
  color: var(--primary);
}

.elo-cell {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  font-weight: 600;
  color: var(--primary);
}

.date-cell {
  color: var(--gray);
  font-size: 0.875rem;
}

.status-badge {
  display: inline-block;
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 600;
}

.status-active {
  background: rgba(76, 175, 80, 0.1);
  color: var(--success);
}

.status-disabled {
  background: rgba(244, 67, 54, 0.1);
  color: var(--danger);
}

.actions-cell {
  width: 120px;
}

.action-buttons {
  display: flex;
  gap: var(--spacing-xs);
}

.btn-small {
  padding: var(--spacing-xs) var(--spacing-sm);
  min-width: auto;
}

.btn-warning {
  background: #FF9800;
  color: var(--white);
}

.btn-warning:hover {
  background: #F57C00;
}

.btn-danger {
  background: var(--danger);
  color: var(--white);
}

.btn-danger:hover {
  background: #d32f2f;
}

.btn-success {
  background: var(--success);
  color: var(--white);
}

.btn-success:hover {
  background: #43a047;
}

.results-count {
  text-align: center;
  color: var(--gray);
  font-size: 0.875rem;
  margin-top: var(--spacing-md);
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-2xl);
  text-align: center;
  color: var(--gray);
}

.empty-state h3 {
  margin: var(--spacing-md) 0 var(--spacing-xs);
  color: var(--secondary);
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: var(--spacing-xl);
}

.confirm-modal {
  background: var(--white);
  border-radius: var(--radius-lg);
  padding: var(--spacing-2xl);
  max-width: 450px;
  width: 100%;
  text-align: center;
}

.modal-icon {
  width: 64px;
  height: 64px;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto var(--spacing-lg);
}

.modal-icon.disable {
  background: rgba(255, 152, 0, 0.1);
  color: #FF9800;
}

.modal-icon.enable {
  background: rgba(76, 175, 80, 0.1);
  color: var(--success);
}

.modal-icon.remove {
  background: rgba(244, 67, 54, 0.1);
  color: var(--danger);
}

.confirm-modal h2 {
  margin-bottom: var(--spacing-md);
}

.confirm-modal p {
  color: var(--gray);
  margin-bottom: var(--spacing-xl);
  line-height: 1.6;
}

.modal-error {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-md);
  background: rgba(244, 67, 54, 0.1);
  border-radius: var(--radius-md);
  color: var(--danger);
  margin-bottom: var(--spacing-lg);
}

.modal-actions {
  display: flex;
  gap: var(--spacing-md);
  justify-content: center;
}

.modal-actions .btn {
  min-width: 140px;
}

.loading-spinner {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top-color: var(--white);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  gap: var(--spacing-md);
  color: var(--gray);
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--light-gray);
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@media (max-width: 1024px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .page-header {
    flex-direction: column;
    gap: var(--spacing-md);
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .filter-controls {
    flex-direction: column;
  }

  .filter-group {
    width: 100%;
  }

  .filter-select {
    width: 100%;
  }
}